
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import PlayerService from "@/store/modules/player/service/player.service";
import SongService from "@/store/modules/song/service/song.service";
import ISong from "@/store/modules/song/interface/song.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import draggable from 'vuedraggable'
import { createToaster } from "@meforma/vue-toaster";

export default defineComponent({
  name: "Songs",
  components: {
	  draggable
  },
  setup() {
    	const router = useRouter();
		const route = useRoute();
		const playerService = new PlayerService();
		const songService = new SongService();
		const songs = ref<Array<any>>([]);
		const site = ref<Object>({});
		const show = ref<Boolean>(false);
		const toaster = createToaster({ position: "bottom-left" });
		const modalSongs = ref<Array<any>>([]);
		const search = ref<String>('');

		const getSongs = (load: boolean) => {
			songs.value = []
			playerService.getSongs(site.value['id'])
			.then((response) => {
				songs.value = response.data
				if(load){
					changeOrder()
				}
			})
			.catch((error) => {
				
			})
		};

		const getData = () => playerService.getSitesById(route.params.id)
		.then((response) => {
			site.value = response.data
			getSongs(false)
		})
		.catch((error) => {
			Swal.fire(
			'Oh no!',
			'Información no encontrada',
			'error'
			);
			router.push({ name: "player-sites" });
		});

		const searchSongs = () => {
			modalSongs.value = []
			playerService.searchSongs(search.value)
			.then((response) => {
				modalSongs.value = response.data
			})
			.catch((error) => {
				
			})
		};

		const existSong = (songId) => {
			let result = false;
			songs.value.forEach(songData => {
				if(songData.id == songId){
					result = true;
				}
			})
			return result;
		}

		const addSong = (songId) => {
			storeSatusSong(songId, 1);
		}

		const removeSong = (songId) => {
			storeSatusSong(songId, 0);
		}

		const storeSatusSong = (songId, status) => playerService.changeStatusSong({song_id: songId, status: status, site_id: site.value['id'] })
		.then((response) => {
			if(status == '1'){
				toaster.success(`Añadido a la lista`);
			}
			else{
				toaster.error(`Removido a la lista`);
			}
			getSongs(true)
		})
		.catch((error) => {
			
		});

		const saveOrder = (listIds)=>{
			playerService.updateOrder({data: listIds})
			.then((response) => {
				toaster.success(`Orden actualizado`);
			})
			.catch((error) => {
				toaster.error(`No se guardaron los cambios`);
			});
		}

		const getIdsOrder = ()=>{
			let listIds: any[] = []
			songs.value.forEach((song: any, index: any) => {
				listIds.push({song_id: song.id, site_id: site.value['id'], order: song.order})
			})
			return listIds
		}

		const changeOrder = ()=>{
			songs.value.forEach((song: any, index: any) => {
				songs.value[index].order = index + 1
			})
			saveOrder(getIdsOrder())
		}

		onMounted(() => {
			getData()
		});

		return {
			site,
			songs,
			modalSongs,
			search,
			searchSongs,
			existSong,
			addSong,
			removeSong,
			changeOrder
		};
  },
});
