import ApiService from "@/core/services/ApiService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class SongService{
  getSites() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/player/sites`, httpOptions());
  }

  getSitesById(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/player/sites/${id}`, httpOptions());
  }

  getSongs(site_id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/player/sites/${site_id}/songs`, httpOptions());
  }

  searchSongs(search: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/player/songs?q=${search}`, httpOptions());
  }

  changeStatusSong(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/player/songs/status`, data, httpOptions());
  }

  updateOrder(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/player/songs/order`, data, httpOptions());
  }
}
